import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { AdminComponent } from './admin/admin.component';
import { SalesexecutiveComponent } from './salesexecutive/salesexecutive.component';
import { ManagementexecutiveComponent } from './managementexecutive/managementexecutive.component';
import { SalesByModelComponent } from './sales-by-model/sales-by-model.component';

const routes: Routes = [ {path:'',redirectTo:'/login', pathMatch:'full'},
{path:'login',component:LoginComponent},
{path:'admin',component:AdminComponent},
{path:'sales_executive',component:SalesexecutiveComponent},
{path:'management_executive',component:ManagementexecutiveComponent},
{path:'sales_by_model',component:SalesByModelComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
