import { Component, OnInit } from '@angular/core';
import { Admin } from '../model/admin';
import { SalesServiceService } from '../Service/sales-service.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {
  userList : Observable<any>;
  public executives=[];
  country;
  updatedUserId;
  updatedExecutive;
  pass
  first;
  constructor(private service:SalesServiceService) { }

  ngOnInit() {

  }
  edit(u:any){
    this.updatedUserId=u[0];
    this.updatedExecutive=u[1];

    console.log(u[0]);
    console.log(u[1]);
    this.first=this.updatedExecutive;
    (<HTMLInputElement>document.getElementById('first')).disabled=false;
    (<HTMLInputElement>document.getElementById('first')).hidden=false;
  }

  onSel(){
    // console.log("select")
    this.country=(<HTMLSelectElement>document.getElementById('country')).value;
    this.service.getExecutives(this.country).subscribe(data=>this.executives=data);
    // console.log(this.executives);
  }

  onSubmit(){
    // this.updatedExecutive=(<HTMLSelectElement>document.getElementById('enable')).value;
    this.pass=(<HTMLSelectElement>document.getElementById('first')).value;
    this.service.updateExecutive(this.updatedUserId,this.pass).subscribe();

    // console.log(this.updatedExecutive);
  }

  onRefresh(){
    this.service.getExecutives(this.country).subscribe(data=>this.executives=data);
  }

}



