import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Login } from '../model/login';
import { Admin } from '../model/admin';
import { Observable } from 'rxjs';
import { SalesServiceService } from '../Service/sales-service.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  
  login: Login;
  validate:any;


  constructor(private router: Router, private service: SalesServiceService) {
    this.login = new Login();
  }
  

  ngOnInit() {

  }

  onSubmit() {
    console.log("Submited")
    // console.log(this.login.userId, this.login.password);
    this.service.getLoginDetails(this.login).subscribe(data=>console.log(data));
    console.log(this.validate);
    this.service.setLoginId(this.login.userId);

    let user = (<HTMLSelectElement>document.getElementById('logintype'));

    if (user.selectedIndex == 1) {
      // if (this.validate == true)
        this.router.navigate(['admin']);
        
      // else
      //   alert("Invalid userID or password");
    }
    else if (user.selectedIndex == 2) {
      // if (this.validate == true)
        this.router.navigate(['sales_executive']);
      // else
      //   alert("Invalid userID or password");
    }
    else if (user.selectedIndex == 3) {
      // if (this.validate == true)
        this.router.navigate(['management_executive']);
      // else
      //   alert("Invalid userID or password");
    }
    else
      alert("Invalid userID or password")
  }


}


