import { Component, OnInit } from '@angular/core';
import { SalesServiceService } from '../Service/sales-service.service';
import { Sales } from '../model/sales';

@Component({
  selector: 'app-salesexecutive',
  templateUrl: './salesexecutive.component.html',
  styleUrls: ['./salesexecutive.component.css']
})
export class SalesexecutiveComponent implements OnInit {
  sales: Sales;
  str: any;
  country: any;
  branchList = [];
  realCountry;
  country1;

  constructor(private salesService: SalesServiceService) {

    this.sales = new Sales();
  }

  ngOnInit() {
    this.salesService.getExecutiveLocation().subscribe(data => this.country = data)

  }

  onFull() {
    // this.realCountry=(<HTMLInputElement>document.getElementById('count')).value;
    if (this.country == 'India') {
      this.branchList = ['Chennai', 'Bangolre', 'Delhi', 'Mumbai']
    }

    else if (this.country == 'Australia') {
      this.branchList = ['Sydney', 'Melbourne']
    }

    else if (this.country == 'France') {
      this.branchList = ['Paris', 'Lyon']
    }

    else if (this.country == 'Germany') {
      this.branchList = ['Berlin', 'Stuttgard', 'Munich']
    }
  }

  onSubmit() {
    this.country1 = ((<HTMLInputElement>document.getElementById('country'))).value;
    this.sales.country = this.country1;
    this.str = this.salesService.registerSalesInfo(this.sales).subscribe();
    this.sales = new Sales();
    console.log(this.sales)

    // console.log(this.str)
    // console.log(this.sales);
  }

}
